@media (max-width:1200px) {
    .row{
        flex-direction: column;
        .col-2{
            width: 100vw;
            .btn{
                width: 33vw;
                margin-top: 5px;
            }
        }
        .col-4{
            width: 100vw;
            input[type="text"]{
                width: 33vw;
            }
        }
    }
}
@media (max-width:600px) {
    .row{
        flex-direction: column;
        .col-2{
            width: 100vw;
            .btn{
                width: 80vw;
                margin-top: 5px;
            }
        }
        .col-4{
            width: 100vw;
            input[type="text"]{
                width: 80vw;
            }
        }
    }
}